@import "normalize.css";
h1,
h2,
h3,
h4,
h5 {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Nunito", sans-serif;
}
ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

p {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 10px;
}
